@use 'base/mixins' as m;
@use 'base/variables' as v;

.bloques {
    @include m.contenedor;
    margin-top: 8rem;

    &__grid {
        @include m.grid(1, 3rem);
        @include m.desktop {
            @include m.grid (2, 3rem);
        }
    }
}

.bloque {
    padding: 3rem;
    color: v.$blanco;
    text-shadow: 1px 1px 1px v.$negro;
    border-radius: 1rem;
    &:nth-child(1) {
        @include m.bg-gradient(#f97316);
    }
    &:nth-child(2) {
        @include m.bg-gradient(#579402);
    }
    &:nth-child(3) {
        @include m.bg-gradient(#a855f7);
    }
    &:nth-child(4) {
        @include m.bg-gradient(#17769e);
    }

    &__heading {
        margin: 1rem 0 2rem 0;
        font-size: 3.2rem;
    }

    &__texto {
        margin: 1rem 0;
        span {
            font-size: 2rem;
            
            font-weight: bold;
            color: v.$verde;
        }

        &--cantidad {
            margin-top: 5rem;
            font-weight: v.$black;
            font-size: 6rem;
            text-align: center;
        }
    }
}