@use 'base/mixins' as m;
@use 'base/variables' as v;

.paginacion {

    margin-top: 5rem;
    margin-bottom: 3rem;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    @include m.tablet {
        justify-content: flex-end;
    }

    &__enlace {
        color: v.$azul;
        font-size: 1.6rem;

        &--texto {
            padding: 1rem;
            border-radius: .5rem;
            border: 1px solid v.$azul;

            &:hover {
                background-color: v.$azul;
                color: v.$blanco;
            }
        }

        &--numero {
            padding: .5rem 1rem;
            border-radius: .5rem;
            margin: 0;
            display: none;
            &:hover {
                background-color: v.$azul;
                color: v.$blanco;
            }
            
            @include m.tablet {
                display: inline;
            }
        }
        &--actual {
            padding: .5rem 1rem;
            margin: 0;
            border-radius: .5rem;
            background-color: v.$azul;
            color: v.$blanco;
            
            display: none;
            @include m.tablet {
                display: inline;
            }
        }
    }
}