@use 'base/mixins' as m;
@use 'base/variables' as v;

.navegacion {
    display: flex;
    flex-direction: column;
    padding: 1rem;

    @include m.desktop {
        flex-direction: row;
        padding: 0;
    }

    &__enlace {
        color: v.$blanco;
        font-size: 2rem;
        padding: 2rem;
        font-weight: v.$black;
        text-transform: uppercase;
        text-align: center;
        border-radius: .5rem;
        transition: 300ms background-color ease, 300ms color ease;
        @include m.desktop {
            border-radius: 0rem;
        }

        &--actual, 
        &:hover {
            background-color: v.$blanco;
            color: v.$azul;
        }
    }
}