@use 'base/mixins' as m;
@use 'base/variables' as v;


.table {
    width: 100%;
    border-collapse: collapse;

    &__thead {
        background-color: v.$gris-oscuro;
        color: v.$blanco;
    }

    &__th {
        padding: 1rem;
        text-align: left;

        &--left {
            border-top-left-radius: .5rem;
            
        }

        &--right {
            border-top-right-radius: .5rem;
            
        }
    }

    &__tr {
        &:nth-child(even) {
            background-color: v.$gris-claro;
        }
        
        &--dashboard-menos {
            &:nth-child(even) {
                background-color: lighten(#a855f7, 10%);
            }
        }
        &--dashboard-mas {
            &:nth-child(even) {
                background-color: lighten(#17769e, 10%);
            }
        }
    }
    
    &__td {
        border-radius: .5rem;
        padding: 1.5rem 1rem;

        &--acciones {
            @extend .table__td;
            display: flex;
            justify-content: flex-end;
            gap: 2rem;
            align-items: center;
        }
    }

    &__tbody {

    }

    &__accion {
        all: unset;
        text-transform: uppercase;
        font-size: 1.4rem;
        font-weight: v.$bold;

        &--editar {
            color: v.$azul;
        }

        &--eliminar {
            color: v.$rojo;
        }

        &:hover {
            cursor: pointer;
        }
    }
}